@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
.blurDiv{display:-webkit-flex;display:flex;margin-top:7em;background:inherit;border-radius:3px;box-shadow:0 10px 30px rgba(255,255,255,0.5);overflow:hidden;background-color:rgba(255,255,255,0.5);-webkit-backdrop-filter:blur(2px);backdrop-filter:blur(2px)}.leftBox{width:20%;float:left}.rightBox{width:80%;float:right}

.canvas{position:absolute;left:0;top:0;z-index:-1;width:100%;height:100%}

.container__PhotoAvatar{padding:1.5em;margin-bottom:12px;box-shadow:0px 5px 3px -5px #000;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center}

.container__SoftSkills{margin-bottom:3px}.container__SoftSkills h3{padding-left:1.9em;padding-top:0.5em}.teste{padding-top:0px;padding-bottom:0px}

.container__HardSkills{margin-bottom:3px}.container__HardSkills h3{margin-top:0em;padding-left:1.9em}

.container__Language{margin-bottom:3px}.container__Language h3{margin-top:0em;padding-left:1.9em}

.container__Hobbies{margin-bottom:3px}.container__Hobbies h3{margin-top:0em;padding-left:1.9em}

.container__NameAndRole{padding:2em;margin-bottom:12px;box-shadow:0px 5px 3px -5px #000}.container__NameAndRole h1{margin-bottom:0.8em}.container__NameAndRole h3{margin-bottom:0}

.container__AcademicHistoric{padding-top:2em;padding-left:2em;padding-right:2em}.container__AcademicHistoric h2{margin-top:0}

.container__ProfessionalHistoric{padding-top:2em;padding-left:2em;padding-right:2em;margin-bottom:12px}.container__ProfessionalHistoric h2{margin-top:0}

.container__Objective{padding-top:2em;padding-left:2em;padding-right:2em}.container__Objective h2{margin-top:0}.container__Objective p{margin-bottom:0}

.container__Contacts{padding-top:0.8em;padding-left:2em;padding-right:2em}.container__Contacts h3{margin-top:0}

/*----- set default style -----*/

html
{
    position: relative;
    margin: 0;
    height: 100%;
    width: 100%;
}

body
{
    font-family: 'Roboto', sans-serif;
    color: #212120;
    margin: 0;
    height: 100%;
    background-image: linear-gradient(180deg,#50AEFF, #ffffff);
    background-repeat: no-repeat;
}
